import React, { useEffect, useState } from "react";
import styles from "./screen.module.css";

const Inputs = (props) => {
  const [fontSize, setFontSize] = useState(32);
  useEffect(() => {
    if (props.showNumber.length > 11) {
      setFontSize(fontSize - 1);
    } else {
      setFontSize(32);
    }
  }, [props.showNumber, props.displayResult]);

  return (
    <screen className={styles.screen}>
      <input
        className={styles.previousOperand}
        type="text"
        readOnly
        value={
          props.result === "" ? "" : props.prevValue + props.displayOperator
        }
      />
      <input
        style={{ fontSize: `${fontSize}px` }}
        className={styles.currentOperand}
        type="text"
        placeholder="0"
        readOnly
        value={props.displayResult === "" && props.showNumber}
      />
    </screen>
  );
};
export default Inputs;
