/* eslint-disable default-case */
import React, { useEffect, useState } from "react";
import Inputs from "./Inputs";
import Keyboard from "./Keyboard";
import styles from "./Calculator.module.css";

const Calculator = (props) => {
  const [prevValue, setPrevValue] = useState("");
  const [nextValue, setNextValue] = useState("");
  const [result, setResult] = useState("");
  const [displayResult, setDisplayResult] = useState("");
  const [flag, setFlag] = useState(true);
  const [showNumber, setShowNumber] = useState("0");

  const [Operator, setOperator] = useState("");
  const [OperatorFlag, setOperatorFlag] = useState(true);
  const [operatorEnable, setOperatorEnable] = useState(true);
  const [displayOperator, setDisplayOperator] = useState("");
  const [equalIsClicked, setEqualIsClicked] = useState(false);

  useEffect(() => {
    setShowNumber(flag === true ? prevValue : nextValue);
    switch (Operator) {
      case "*":
        setResult(parseFloat(prevValue) * parseFloat(nextValue));
        setDisplayOperator(" x");
        break;
      case "+":
        setResult(parseFloat(prevValue) + parseFloat(nextValue));
        setDisplayOperator(" +");
        break;
      case "-":
        setResult(parseFloat(prevValue) - parseFloat(nextValue));
        setDisplayOperator(" -");
        break;
      case "/":
        setResult(parseFloat(prevValue) / parseFloat(nextValue));
        setDisplayOperator(" รท");
        break;
      case "%":
        setResult(parseFloat(prevValue) % parseFloat(nextValue));
        setDisplayOperator(" %");
        break;
    }
  }, [prevValue, nextValue, result, Operator, displayOperator]);

  const reset = (e) => {
    setPrevValue(
      e.target.getAttribute("type") === "reset" ? "" : result.toString()
    );
    setEqualIsClicked(false);
    setNextValue("");
    setResult("");
    setDisplayResult("");
    setFlag(true);
    setShowNumber("");
    setOperator("");
    setOperatorFlag(true);
    setOperatorEnable(true);
    setDisplayOperator("");
  };
  const switchValue = (e) => {
    if (flag) {
      if (prevValue.toString().includes("-")) {
        setPrevValue(prevValue.slice(1));
      } else {
        if (prevValue.toString().length <= 0) return;
        setPrevValue("-" + prevValue);
      }
    } else if (flag === false) {
      if (nextValue.toString().includes("-")) {
        setNextValue(nextValue.slice(1));
      } else {
        if (nextValue.toString().length <= 0) return;
        setNextValue("-" + nextValue);
      }
    }
  };
  const operatorHandler = (e) => {
    if (prevValue === "0.") return;
    setOperatorEnable(false);
    setShowNumber("");
    setDisplayResult("");
    setShowNumber("");
    setPrevValue(prevValue);
    if (OperatorFlag === false) {
      setPrevValue(result);
      setNextValue("");
      setOperator(e.target.value);
    } else {
      setOperatorFlag(false);
      setFlag(!flag);
      setOperator(e.target.value);
    }
  };
  const enterNumber = (e) => {
    if (flag) {
      if (equalIsClicked === true) {
        if (e.target.value === "0") return;
        setPrevValue(
          e.target.value === "." ? "0" + e.target.value : e.target.value
        );
        setEqualIsClicked(false);
        return;
      }
      if (e.target.value === "0" && prevValue === "") return;
      if (e.target.value === "." && prevValue.includes(".")) return;
      setPrevValue(
        e.target.value === "." && prevValue === ""
          ? prevValue + "0" + e.target.value
          : prevValue + e.target.value
      );
    } else {
      if (e.target.value === "." && nextValue.includes(".")) return;
      setOperatorEnable(true);
      setNextValue(
        e.target.value === "." && nextValue === ""
          ? nextValue + "0" + e.target.value
          : nextValue + e.target.value
      );
    }
  };
  const equalHandler = (e) => {
    reset(e);
    setEqualIsClicked(true);
  };

  const keyHandler = (e) => {
    if (e.target.getAttribute("type") === "switch") {
      switchValue();
    }
    if (e.target.getAttribute("type") === "number") {
      enterNumber(e);
    }
    if (e.target.getAttribute("type") === "reset") {
      reset(e);
    }
    if (e.target.getAttribute("type") === "operator") {
      if (operatorEnable === false || prevValue === "") return;
      operatorHandler(e);
    }
    if (e.target.getAttribute("type") === "equal") {
      if (prevValue === "" || nextValue === "") return;
      equalHandler(e);
    }
  };
  return (
    <>
      <calculator className={styles.calculator}>
        <Inputs
          result={result}
          prevValue={prevValue}
          showNumber={showNumber}
          displayResult={displayResult}
          displayOperator={displayOperator}
        ></Inputs>
        <Keyboard click={keyHandler}></Keyboard>
      </calculator>
    </>
  );
};
export default Calculator;
